import React from 'react'
import styled from 'styled-components'
import { Button } from '@mui/material'
import Contact from '../../../components/Contact'
import PaymentMethod from '../../../components/PaymentMethod'
import Airspot from '../../../components/Airspot'
import PackageSize from '../../../components/PackageSize'

const Container = styled.div`
  display: flex;
  flex-direction: column;
`
const Sum = styled.p`
  font-size: 130%;
`
export default function Cart({ selection, onDone }) {
  const { contact, packageSize, airspot, paymentMethod } = selection
  const { price } = packageSize
  const [euro, cents = ''] = price.toString().split('.')

  return (
    <Container>
      <h2>Angaben prfen</h2>
      <p>
        berprfe noch einmal deine Auswahl.
        <br />
        Alles korrekt? Dann schliee den Vorgang zahlungspflichtig ab.
      </p>
      <p>
        <b>Gewhlte Paketgre:</b>
      </p>
      <PackageSize packageSize={packageSize} square={false} />
      <p>
        <b>Ziel:</b>
      </p>
      <Contact contact={contact} />
      <p>
        <b>Zahlungsmethode:</b>
      </p>
      <PaymentMethod method={paymentMethod} />
      <p>
        <b>Airspot:</b>
      </p>
      <Airspot airspot={airspot} />
      <br />
      <h2>Gesamtsumme</h2>
      <Sum>
        <b>{`${euro},${cents.padEnd(2, '0')}`}</b>
      </Sum>
      <Button
        variant="contained"
        color="primary"
        onClick={onDone}
        id="cart-confirm"
      >
        Kostenpflichtig abschlieen
      </Button>
      <br />
      <br />
    </Container>
  )
}
